import React, { memo } from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { Button } from 'core/src/Button';
import { SizeVariant } from 'core/types/common';
import { TextSize } from 'core/src/typography';

import { ReactComponent as IconDownload } from '../../img/download.svg';

import styles from './styles.scss';

const cd = cssDecorator(styles);

interface ReadyScreenProps {
  /** Текст на кнопке, которая открывает диалоговое окно выбора файла */
  textButton: string;

  /** Обработчик нажатия на кнопку Загрузки */
  onClick: VoidFunction;

  /** Размер кнопки загрузки */
  buttonSize?: SizeVariant;

  /** Размер текста */
  textSize?: TextSize;

  /** Описание разрешенных для загрузки типов файлов */
  mediaTypes?: string;

  /** Заблокирован? */
  disabled?: boolean;
}

/** Экран выбора файла для загрузки */
function UploadScreen({
  textButton,
  onClick,
  buttonSize = 'default',
  textSize = 'default',
  mediaTypes,
  disabled
}: ReadyScreenProps): JSX.Element {
  return (
    // eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions
    <div
      onClick={ disabled ? undefined : onClick }
      className={ styles.container }
      data-test-id="inputFile_uploadScreen"
    >
      <IconDownload className={ styles.icon_download } />
      <div
        className={ cd('types_name', { [textSize]: true }) }
        data-test-id="inputFile_mediaType"
      >
        { mediaTypes }
      </div>
      <Button
        text={ textButton }
        size={ buttonSize }
        type="secondary"
        disabled={ disabled }
      />
    </div>
  );
}

const MemoUploadScreen = memo(UploadScreen);
MemoUploadScreen.displayName = 'UploadScreen';

export default MemoUploadScreen;
